body {
    margin: 0;
    padding: 0;
    background-color: lightgray; 
}

.calendar-generator {
    text-align: left;
    font-family: "Times New Roman", Times, serif;
    white-space: pre-wrap;
    padding: 7px;   
}

/* Input container */
.input-container {
margin-bottom: 20px;
}

.input-container input,
.input-container select {
padding: 5px;
font-size: 14px;
}

/* Display options */
.display-options {
margin-bottom: 20px;
}

.display-options button {
padding: 5px 10px;
font-size: 14px;
border: none;
cursor: pointer;
}

.display-options-menu {
margin-top: 10px;
padding: 10px;
border: 1px solid #ccc;
background-color: #f9f9f9;
}

.language-dropdown{
display: inline-block;
}
.display-options-menu label {
display: inline;
margin-bottom: 5px;
}

.language-dropdown select {
padding: 5px;
font-size: 14px;
}

.swap-label {
display: block;
margin-top: 10px;
}

/* Columns table */
.columns-table {
margin-top: 20px;
}

#monthSelect{
padding: 5px;
font-size: 14px;
}

/* Calendar text */
.calendar-text {
font-size: 14px;
background-color: #f9f9f9;
padding: 10px;
border: 1px solid #ccc;
}

/* Footer */
footer {
margin-top: 20px;
text-align: left;
font-size: 15px;
color: #777;
}

  